<template>
    <div class="container-fluid">
        <form v-on:submit.prevent="Register">
            <!--  -->
            <div class="form-group">
                <span>*</span>
                <label for="accno"></label>
                <input class="form-control" type="text" id="accno" v-model="accno" />
            </div>
            <!--  -->
            <div class="form-group">
                <label for="name"></label>
                <input
                    class="form-control"
                    type="text"
                    id="name"
                    v-model="name"
                    placeholder=""
                />
            </div>
            <!--  -->
            <div class="form-group">
                <label for="telno"></label>
                <input
                    class="form-control"
                    type="text"
                    id="telno"
                    v-model="telno"
                    placeholder=""
                />
            </div>
            <!--  -->
            <div class="form-group">
                <span>*</span>
                <label for="password"></label>
                <input class="form-control" type="password" id="password" v-model="password" />
            </div>
            <!--  -->
            <div class="form-group">
                <span>*</span>
                <label for="confirmPassword"></label>
                <input
                    class="form-control"
                    type="password"
                    id="confirmPassword"
                    v-model="confirmPassword"
                />
            </div>
            <!--  -->
            <button type="submit" class="btn btn-primary btn-content"></button>
        </form>
        <button class="btn btn-default btn-content" @click="goBack()"></button>
    </div>
</template>

<script>
export default {
    data() {
        return {
            accno: null,
            name: null,
            telno: null,
            password: null,
            confirmPassword: null
        };
    },
    methods: {
        Register() {
            if (this.parameterCalibration() == false) {
                return;
            }
            this.$axios
                .post("register", {
                    accno: this.accno,
                    name: this.name,
                    telno: this.telno,
                    password: this.password
                })
                .then(
                    result => {
                        if (result.data.status == 1) {
                            alert("");
                            this.$router.push({
                                name: "login"
                            });
                        } else {
                            alert(result.data.message);
                            return;
                        }
                    },
                    error => {
                        alert(
                            "Error:\n" +
                                error
                        );
                        return;
                    }
                );
        },
        // 
        parameterCalibration() {
            if (this.accno == null || this.accno.trim().length == 0) {
                alert("");
                return false;
            }
            if (this.password == null || this.password.trim().length == 0) {
                alert("");
                return false;
            }
            if (this.confirmPassword != this.password) {
                alert("");
                return false;
            }
            return true;
        },
        // 
        goBack() {
            this.$router.push({
                name: "login"
            });
        }
    }
};
</script>

<style scoped>
.container-fluid {
    margin-top: 15px;
}

.btn {
    width: 100%;
    margin-bottom: 15px;
}

.btn-content {
    letter-spacing: 25px;
    text-indent: 25px;
}

span {
    color: red;
}

/*  input  */
input::-webkit-input-placeholder {
    font-style: italic;
}

input:-moz-placeholder {
    font-style: italic;
}

input::-moz-placeholder {
    font-style: italic;
}

input:-ms-input-placeholder {
    font-style: italic;
}
</style>